/**
 * Vendors
 */
@import 'vendor/materialize.less';
// @import 'vendor/font-awesome.less';
@import 'vendor/video-js.less';

/**
 * Custom shit
 */
@font-face {
    font-family: 'Sacramento';
    src: url('/font/Sacramento-Regular.ttf') format('truetype');
    font-style: cursive;
}
@import 'helpers.less';

html {
    position: relative;
    min-height: 100%;
}

body {
    background-color: #fff;
    text-rendering: optimizeLegibility;

    #content a {
        font-weight: 300;
        color: #494949;
    }

    a:hover {
        border-radius: 5px;
    }

    &#videoPage {
        color: white;
        margin-top: 53px;
    }
}

@import 'module/branding';
@import 'module/navbar';
// @import 'module/home';
@import 'module/sidebar';
@import 'module/video-cards';
@import 'module/video-page';

.footer {
    position: absolute;
    bottom: 0;
    z-index: 50;

    width: 100%;
    height: 60px;

    margin-top: 60px;
    background-color: #3c708d;

    a:hover {
        font-weight: bold;
        color: #bec6cd;
        text-decoration: none;
        text-shadow: 5px 1px 1px #296281;
    }

    & > .container {
        margin-top: 20px;
        // margin-bottom: 20px;
    }
}
